const portNum = window.portNum;
const portType = window.portType;
const portCode = window.portCode;

export const portData = [];

for(let i = 0; i < portNum.length; i++){
  const data = {
    portNumber: portNum,
    portShape: portType,
    portCode: portCode,
  }

  portData.push(data);
}


