const code = window.code;
const name = window.name;
const address = window.address;
const install_date = window.install_date;
const picture = window.picture;
const admin = window.admin;
const install = window.install;


export const stationData = [
  {
    stationNumber: code,
    stationName: name,
    installAddress: address,
    installDate: install_date,
    installPicture: picture,
    installCompany: admin,
    state: install,
  }
]


