import { Fragment, useState } from "react";
import { stationData } from "../data/detailManagementStations";
import { portData } from "../data/detailManagementPortList";
import select_icon from "../images/select_icon2.png";
import DeleteButton from "./DeleteButton";
import DropBox from "./DropBox";
import TwoLineContainer from "./TwoLineContainer";
import eightMMGrayIcon from "../images/8mm_gray.png";
import sixteenMMGrayIcon from "../images/16mm_gray.png";
import eightMMBlackIcon from "../images/8mm_black.png";
import sixteenMMBlackIcon from "../images/16mm_black.png";

const s_type = window.type;
const smps = window.spms;
const panel = window.panel;
const battery = window.battery;

function DetailStation() {
  const StationInfoTable = (props) => {
    return (
      <table className="detailStation-table">
        <thead>
          <tr>
            <td>스테이션 번호</td>
            <td>스테이션 명</td>
            <td>설치 주소</td>
            <td>설치일자</td>
            <td>설치장소 사진</td>
            <td>설치업체</td>
            <td>운영상태</td>
          </tr>
        </thead>
        <tbody>
          <StationInfoChange {...props} />
        </tbody>
      </table>
    );
  };

  const StationInfoChange = (props) => {
    const headStyle = {
      boxSizing: "border-box",
      width: "100%",
      height: "27px",
      border: "1px solid #252525",
      borderRadius: "5px",
      padding: "0 8px",
    };

    const bodyStyle = {
      boxSizing: "border-box",
      width: "100%",
      top: "2.3em",
      border: "1px solid #252525",
      borderRadius: "5px",
      paddingTop: "4px",
      paddingBottom: "1px",
      backgroundColor: "white",
      boxShadow: "0 2px 5px rgba(0, 0, 0, 0.3)",
    };

    const itemStyle = {
      padding: "0 8px",
      color: "black",
      marginBottom: "3px",
    };

    if (props.editMode === false) {
      return (
        <tr>
          {stationData.map((value, index) => {
            return (
              <Fragment key={index}>
                <td>{value.stationNumber}</td>
                <td>{value.stationName}</td>
                <td>{value.installAddress}</td>
                <td>{value.installDate}</td>
                <td>{value.installPicture}</td>
                <td>{value.installCompany}</td>
                <td>
                  {(() => {
                    if (value.state === 0) {
                      return "운용 중";
                    } else if (value.state === 1) {
                      return "철거";
                    }
                  })()}
                </td>
              </Fragment>
            );
          })}
        </tr>
      );
    } else if (props.editMode === true) {
      return (
        <tr>
          <td className="active">
            <input defaultValue={stationData[0].stationNumber}></input>
          </td>
          <td className="active">
            <input defaultValue={stationData[0].stationName}></input>
          </td>
          <td className="active">
            <input defaultValue={stationData[0].installAddress}></input>
          </td>
          <td className="active">
            <input defaultValue={stationData[0].installDate}></input>
          </td>
          <td className="active" style={{ display: "flex" }}>
            <div className="tiny-button">
              <span>등록</span>
            </div>
            <div className="tiny-button">
              <span>삭제</span>
            </div>
          </td>
          <td className="active">
            <input defaultValue={stationData[0].installCompany}></input>
          </td>
          <td className="active">
            <DropBox
              defaultIndex={stationData[0].state}
              headStyle={headStyle}
              bodyStyle={bodyStyle}
              itemStyle={itemStyle}
              icon={select_icon}
            >
              <div>운용 중</div>
              <div>철거</div>
            </DropBox>
          </td>
        </tr>
      );
    } else {
      return null;
    }
  };

  const StationTypeTable = (props) => {
    let type;

    if (s_type === 0) {
      type = "water";
    } else if (s_type === 1) {
      type = "sun";
    }

    const [stationType, setStationType] = useState(type);
    const Th = () => {
      if (stationType === "water") {
        return (
          <tr>
            <td>발전방식</td>
            <td>SMPS 스펙</td>
          </tr>
        );
      } else if (stationType === "sun") {
        return (
          <tr>
            <td>발전방식</td>
            <td>패널 스펙</td>
            <td>배터리 스펙</td>
          </tr>
        );
      }
    };

    const setStationTypeWater = () => {
      setStationType("water");
    };

    const setStationTypeSun = () => {
      setStationType("sun");
    };

    const onClick = [setStationTypeWater, setStationTypeSun];

    return (
      <table className="detailStation-table stationType-table">
        <thead>
          <Th />
        </thead>
        <tbody>
          <StationTypeChange
            {...props}
            stationType={stationType}
            onClick={onClick}
          />
        </tbody>
      </table>
    );
  };

  const StationTypeChange = (props) => {
    const headStyle = {
      boxSizing: "border-box",
      width: "100%",
      height: "27px",
      border: "1px solid #252525",
      borderRadius: "5px",
      paddingLeft: "7px",
      paddingRight: "8px",
    };

    const bodyStyle = {
      boxSizing: "border-box",
      width: "100%",
      top: "2.3em",
      border: "1px solid #252525",
      borderRadius: "5px",
      paddingTop: "5px",
      paddingBottom: "2px",
      backgroundColor: "white",
      boxShadow: "0 2px 5px rgba(0, 0, 0, 0.3)",
    };

    const itemStyle = {
      padding: "0 8px",
      color: "black",
      marginBottom: "3px",
    };

    if (props.stationType === "water") {
      if (props.editMode === false) {
        return (
          <tr>
            <td>수전</td>
            <td>{smps}</td>
          </tr>
        );
      } else if (props.editMode === true) {
        return (
          <tr>
            <td className="active">
              <DropBox
                defaultIndex={0}
                headStyle={headStyle}
                bodyStyle={bodyStyle}
                itemStyle={itemStyle}
                icon={select_icon}
                onClick={props.onClick}
              >
                <div>수전</div>
                <div>태양광</div>
              </DropBox>
            </td>
            <td className="active">
              <input defaultValue="12V 30A"></input>
            </td>
          </tr>
        );
      }
    } else if (props.stationType === "sun") {
      if (props.editMode === false) {
        return (
          <tr>
            <td>태양광</td>
            <td>{panel}</td>
            <td>{battery}</td>
          </tr>
        );
      } else if (props.editMode === true) {
        return (
          <tr>
            <td className="active">
              <DropBox
                defaultIndex={1}
                headStyle={headStyle}
                bodyStyle={bodyStyle}
                itemStyle={itemStyle}
                icon={select_icon}
                onClick={props.onClick}
              >
                <div>수전</div>
                <div>태양광</div>
              </DropBox>
            </td>
            <td className="active">
              <input defaultValue="420W"></input>
            </td>
            <td className="active">
              <input defaultValue="4800W"></input>
            </td>
          </tr>
        );
      }
    }
  };

  const StationPortTable = (props) => {
    return (
      <table className="detailStation-table port-table">
        <thead>
          <tr>
            <td>포트넘버</td>
            <td>단자 모양</td>
            <td>QR코드값</td>
          </tr>
        </thead>
        <tbody>
          <StationPortChange {...props} />
        </tbody>
      </table>
    );
  };

  const StationPortChange = (props) => {
    const headStyle = {
      boxSizing: "border-box",
      width: "100%",
      height: "27px",
      border: "1px solid #252525",
      borderRadius: "5px",
      paddingLeft: "7px",
      paddingRight: "8px",
    };

    const bodyStyle = {
      boxSizing: "border-box",
      width: "100%",
      top: "2.3em",
      border: "1px solid #252525",
      borderRadius: "5px",
      paddingTop: "5px",
      paddingBottom: "2px",
      backgroundColor: "white",
      boxShadow: "0 2px 5px rgba(0, 0, 0, 0.3)",
    };

    const itemStyle = {
      padding: "0 7px",
      color: "black",
      marginBottom: "3px",
    };

    if (props.editMode === false) {
      return portData.map((value, index) => {
        return (
          <tr key={index}>
            <td>{index + 1}</td>
            <td>
              {(() => {
                if (value.portShape === 0) {
                  return (
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        height: "25px",
                      }}
                    >
                      <img
                        style={{
                          position: "relative",
                          top: "-1px",
                          marginRight: "5px",
                        }}
                        src={eightMMGrayIcon}
                        alt="eightMM"
                      ></img>
                      <span style={{ position: "relative", top: "-1px" }}>
                        DC 8mm
                      </span>
                    </div>
                  );
                } else if (value.portShape === 1) {
                  return (
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        height: "25px",
                      }}
                    >
                      <img
                        style={{
                          position: "relative",
                          top: "-1px",
                          marginRight: "5px",
                        }}
                        src={sixteenMMGrayIcon}
                        alt="sixteenMM"
                      ></img>
                      <span style={{ position: "relative", top: "-1px" }}>
                        항공 16mm
                      </span>
                    </div>
                  );
                }
              })()}
            </td>
            <td>
              <span style={{ position: "relative", top: "-1px" }}>
                {value.qrCode}
              </span>
            </td>
          </tr>
        );
      });
    } else if (props.editMode === true) {
      return portData.map((value, index) => {
        return (
          <tr key={index}>
            <td>{index + 1}</td>
            <td className="active">
              <DropBox
                defaultIndex={value.portShape}
                icon={select_icon}
                headStyle={headStyle}
                bodyStyle={bodyStyle}
                itemStyle={itemStyle}
              >
                <div>
                  <img
                    style={{ marginRight: "5px" }}
                    src={eightMMBlackIcon}
                    alt="eightMM"
                  ></img>
                  <span>DC 8mm</span>
                </div>
                <div>
                  <img
                    style={{ marginRight: "5px" }}
                    src={sixteenMMBlackIcon}
                    alt="sixteenMM"
                  ></img>
                  <span>항공 16mm</span>
                </div>
              </DropBox>
            </td>
            <td className="active">
              <input defaultValue={value.qrCode}></input>
            </td>
          </tr>
        );
      });
    }
  };

  return (
    <div className="detailStation-container">
      <form style={{ marginBottom: "20px" }}>
        <TwoLineContainer
          hasImg="edit"
          title="스테이션 정보"
          className="stationInfo-container"
        >
          <StationInfoTable />
        </TwoLineContainer>
      </form>
      <form style={{ marginBottom: "20px" }}>
        <TwoLineContainer
          hasImg="edit"
          title="발전 정보"
          className="stationType-container"
        >
          <StationTypeTable />
        </TwoLineContainer>
      </form>
      <form>
        <TwoLineContainer
          hasImg="edit"
          title="포트 정보"
          className="stationPort-container"
        >
          <StationPortTable />
        </TwoLineContainer>
      </form>
      <DeleteButton />
    </div>
  );
}

export default DetailStation;
